            <b-col cols="3" class="d-flex align-items-center">
              <b-container class="bv-example-row">
                <b-row>
                  <b-col class="d-flex justify-content-center" style="position: relative">
                    <i v-if="id == undefined" v-b-tooltip.hover.bottom="$t('Change Profile Picture')" class="fa-solid fa-camera-retro fa-xl" style="
                        position: absolute;
                        z-index: 10;
                        bottom: 10px;
                        right: 40px;
                        cursor: pointer;
                      " @click="changeProfilePic"></i>
                    <input type="file" ref="fileInput" accept="image/jpeg, image/png" @change="handleFileChange" style="display: none" />
                    <img class="imageProfile" :src="profile.image === null
                        ? require(`@/assets/images/user.png`)
                        : profile.image.url
                      " />
                  </b-col>
                </b-row>
              </b-container>
            </b-col>
            <!-- column 2 -->
            <b-col cols="9">
              <b-row class="saj-title m-0 mb-1 mt-1 p-0">
                <p class="ellipsisProfile" style="font-weight: 600">
                  {{ profile.full_name }}
                </p>
              </b-row>
              <b-row>
                <b-col md="4">
                  <p class="saj-text d-flex">
                    <feather-icon v-b-tooltip.hover.bottom="$t('Email')" class="mr-1" icon="MailIcon" size="22" stroke="black" />
                    <span class="ellipsisProfile">
                      {{ profile.email }}
                    </span>
                  </p>
                </b-col>
                <b-col md="4">
                  <p class="saj-text d-flex">
                    <feather-icon v-b-tooltip.hover.bottom="$t('Position')" class="mr-1" icon="UserIcon" size="22" stroke="black" />
                    <span class="ellipsisProfile">
                      {{ position }}
                    </span>
                  </p>
                </b-col>
                <b-col md="4">
                  <p class="saj-text d-flex">
                    <feather-icon v-b-tooltip.hover.bottom="$t('Phone Number')" class="mr-1" icon="PhoneIcon" size="22" stroke="black" />
                    <span class="ellipsisProfile"> {{ mobile_number }} </span>
                  </p>
                </b-col>
                <b-col md="4">
                  <p class="saj-text d-flex">
                    <feather-icon v-b-tooltip.hover.bottom="$t('Employee Id')" class="mr-1" icon="ClipboardIcon" size="22" stroke="black" />
                    <span class="ellipsisProfile"> {{ employee_number }} </span>
                  </p>
                </b-col>

