                    <feather-icon v-b-tooltip.hover.bottom="$t('Hire Date')" class="mr-1" icon="CalendarIcon" size="22" stroke="black" />
                    <span class="ellipsisProfile">
                      {{ $t(DateChanged(hire_date)) }}
                    </span>
                  </p>
                </b-col>
              </b-row>
            </b-col>
          </b-row>
        </b-container>
      </b-card>

