                                      <b-col cols="10">
                                        <b-form-group label-for="defaultLabel">
                                          <b-form-input v-model="profile.address2" :disabled="isDisabled" class="saj-text" />
                                        </b-form-group>
                                      </b-col>
                                    </b-col>
