                                    class="saj-text m-0"
                                    :disabled="isDisabled"
                                  /> -->
                                      <b-form-select v-model="profile.position" :options="positionList" :value="positionList" :placeholder="$t('Select Position')" size="lg" class="saj-text" rows="8" disabled />
                                      <small class="text-danger">{{
                                        errors[0]
                                      }}</small>
                                    </validation-provider>
                                  </b-form-group>
                                </b-col>
