                </b-tab>
                <b-tab :title="$t('Education')">
                  <b-col v-if="roles.isAdmin || isHasEducation" style="display: flex; justify-content: flex-end">
                    <b-button style="
                        background: #0b103c !important;
                        border-color: #0b103c !important;
                      " class="saj-button mr-1" @click="educationInfo()">
