                      <b-form ref="form" :style="{ height: trHeight }" class="repeater-form" @submit.prevent="repeateAgain">
                        <!-- Row Loop -->
                        <div v-if="isEduInfo">
                          <b-row v-for="(existitem, i) in existitems" :id="existitem.id" :key="'a' + i" ref="row">
