                              <b-form-group :label="$t('Qualification')" class="saj-subtitle">
                                <b-form-select v-model="existitem.qualification" class="saj-text" :options="isEnglish ? quaoptions_bi : quaoptions
                                  " :disabled="isDisabled" />
                              </b-form-group>
                            </b-col>

                            <!-- Institusi -->
                            <b-col md="3">
                              <b-form-group :label="$t('Institute')" class="saj-subtitle">
