                              <feather-icon style="
                                  width: 30px;
                                  height: 30px;
                                  color: #c5c7cd;
                                  margin-top: 30px;
                                " icon="MinusCircleIcon" @click="
                                  showAlertModal(
                                    'edu-task',
                                    (val = existitem.eduId)
                                  )
                                  " />
                            </b-col>
                            <!-- display attachment -->
                            <b-row class="d-flex justify-content-start align-items-center m-0 ml-3 saj-subtitle" style="color: #000000; width: 100%">
                              <div v-if="existitem.file === null" class="row align-items-center" style="font-size: 0.8rem">
                                <feather-icon class="mr-1" icon="PaperclipIcon" size="16" />
                                {{ $t("No File") }}
                              </div>
                              <div v-if="existitem.file !== null" class="row align-items-center" style="font-size: 0.8rem">
                                <feather-icon class="mr-1" icon="PaperclipIcon" size="16" />
