                                  {{ existitem.file.name }}
                                </a>
                              </div>
                            </b-row>

                            <b-col cols="12">
                              <hr />
                            </b-col>
                          </b-row>
                        </div>
                        <div v-else class="saj-subtitle" style="font-style: italic">
