                            {{ $t("No Education Record Retrieved") }}
                          </div>
                        </div>
                      </b-form>
                    </validation-observer>
                    <b-modal id="delete-edu" :hide-footer="true" :hide-header="true" :centered="true">
                      <alert-modal :title="$t('Click Yes to delete Education')" confirm-btn="Yes" @cancel="hideAlertModal('edu-task')" @confirm="deleteEducation(deleteIdEdu)" />
                    </b-modal>
                  </div>
                  <!-- Add New Education -->
                  <div v-if="roles.isAdmin" class="m-2">
