                            <b-col md="3">
                              <b-form-group :label="$t('Qualification')" class="">
                                <validation-provider #default="{ errors }" name="Qualification " rules="required">
                                  <b-form-select v-model="item.qualification" class="saj-text" :options="isEnglish ? quaoptions_bi : quaoptions
                                    " />
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </b-form-group>
                            </b-col>

                            <!-- Institusi -->
                            <b-col md="3">
                              <b-form-group :label="$t('Institute')" class="">
                                <validation-provider #default="{ errors }" :name="$t('Institute')" rules="required">
                                  <b-form-input v-model="item.institute" class="saj-text" type="text" :placeholder="$t('Enter institute')" />
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </b-form-group>
                            </b-col>

                            <!-- Bidang Pengajian -->
                            <b-col md="3">
                              <b-form-group :label="$t('Field of study')" class="">
                                <validation-provider #default="{ errors }" :name="$t('Field of study')" rules="required">
                                  <b-form-input v-model="item.field_of_study" :placeholder="$t('Enter field of study')" class="saj-text" type="text" />
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </b-form-group>
                            </b-col>

                            <!-- Tahun Graduan -->
                            <b-col md="3">
                              <b-form-group :label="$t('Year of Graduation')" class="">
