                                  <!-- {{ item.year_of_graduate }} -->
                                  <Datepicker id="input-id" v-model="item.year_of_graduate" input-class="my-picker-class" :value="defaultDate" :format="DatePickerFormat" type="number" minimum-view="year" name="datepicker" :typeable="true" class="saj-text" />
                                  <small class="text-danger">{{
                                    errors[0]
                                  }}</small>
                                </validation-provider>
                              </b-form-group>
                            </b-col>

                            <!-- File -->
                            <b-col md="3">
                              <validation-provider #default="{ errors }" :name="$t('File')" rules="required">
                                <b-form-file ref="fileValidation" v-model="item.fileToUpload" type="file" class="mt-1 saj-text" :browse-text="$t('Choose File')" :placeholder="$t('No file choosen')" style="width: 100%; text-align: start" @change="onFileChangeEdu" />
                                <small class="text-danger">{{
                                  errors[0]
                                }}</small>
                              </validation-provider>
                            </b-col>
                            <!-- File end -->
                          </b-row>
                          <b-col cols="12">
                            <hr />
                          </b-col>
                        </div>
                      </b-form>
                    </validation-observer>
                  </div>
                </b-tab>
                <b-tab :title="$t('Employment History')">
                  <b-overlay :show="show" rounded="sm">
                    <template #overlay>
                      <div class="d-flex flex-column align-items-center justify-content-center">
                        <b-spinner variant="primary" label="Spinning" />
                        <p>{{ $t("Fetching Data") }}...</p>
                      </div>
                    </template>
                    <div class="row p-2">
                      <b-table class="styleTable mt-1" style="" :items="historyList" :fields="fields" :per-page="perPage" :current-page="currentPage" label-size="lg" bordered show-empty responsive>
                        <template #head()="data">
                          <span class="saj-text d-flex justify-content-center">{{ $t(data.label) }}</span>
                        </template>
                        <template #cell(index)="data">
                          <div class="saj-text d-flex justify-content-center">
                            {{
                              currentPage === 1
                                ? data.index + 1
                                : data.index + 1 + (currentPage - 1) * 10
                            }}
                          </div>
                        </template>
                        <template #empty="">
                          <h4 style="text-align: center; font-style: italic">
                            {{ $t("There are no records to show") }}
                          </h4>
                        </template>
                        <template #cell(date)="data">
                          <div class="saj-text" style="">
                            {{ DateChanged(data.item.date) }}
                          </div>
                        </template>
                        <template #cell(previous_subsidiary_name)="data">
                          <div class="saj-text" style="">
                            {{ data.item.subsidiary_name }}
                          </div>
                        </template>
                        <template #cell(prev_department_name)="data">
                          <div class="saj-text" style="">
                            {{ data.item.department_name }}
                          </div>
                        </template>
                        <template #cell(prev_position)="data">
                          <div class="saj-text" style="">
                            {{ data.item.position }}
                          </div>
                        </template>
                        <template #cell(subsidiary_name)="data">
                          <div class="saj-text" style="">
                            {{ data.item.subsidiary_name }}
                          </div>
                        </template>
                        <template #cell(type)="data">
                          <div class="saj-text" style="">
                            {{ $t(data.item.type) }}
                          </div>
                        </template>
                        <template #cell(action)="data">
                          <div class="saj-text" style="">
                            <!-- {{ data.item.transfer_id }} -->
                            <div>
                              <feather-icon v-b-modal.transfer-details icon="EyeIcon" color="purple" class="hoverIcon" size="25" @click="transferID = data.item.transfer_id" />
                            </div>
                          </div>
                        </template>
                      </b-table>
                    </div>
                    <b-row v-if="historyList.length > 0">
                      <b-col class="d-flex justify-content-start">
                        <span class="saj-text">
                          {{ $t("Showing") }}
                          {{
                            rows > perPage * currentPage
                              ? perPage * currentPage
                              : rows
                          }}
                          {{ $t("from") }} {{ rows }} {{ $t("entries") }}
                          <!-- {{ $t("Showing") }} 1 {{ $t("to") }} 2 {{ $t("of") }} 3 {{ $t("entries") }} -->
                        </span>
                      </b-col>
                      <b-col>
                        <b-pagination v-model="currentPage" :total-rows="rows" :per-page="perPage" pills class="saj-text mb-1 justify-content-end" />
                      </b-col>
                    </b-row>
                  </b-overlay>
                  <b-modal id="transfer-details" :hide-header="true" :hide-footer="true" :centered="true" size="lg">
                    <!-- $bvModal.hide(nama modal id) untuk tutup modal-->
                    <emp-details :transfer="transferID" @close="$bvModal.hide('transfer-details')" @transfer-details="$bvModal.hide('transfer-details')" />
                  </b-modal>
                </b-tab>
                <!-- <b-tab title="Disable" disabled /> -->
              </b-tabs>
            </b-card>
          </b-col>
        </b-row>
      </section>
    </b-overlay>
  </div>
</template>

