                year_of_graduate:
                  response.data.data.employee.educations[i].year_of_graduate,
                file: response.data.data.employee.educations[i].file,
                fileToUpload: null,
                eduId: response.data.data.employee.educations[i].id,
              });
            }
          } else {
            this.isEduInfo = false;
          }
        });
